// components/slate-description/page.tsx
'use client';
import { useState, useMemo, useCallback } from 'react';
import { Slate, Editable, withReact } from 'slate-react';
import { createEditor, Descendant, Node } from 'slate';
import { withHistory } from 'slate-history';

const SlateEditor = () => {
  // Initialize the editor
  const editor = useMemo(() => withHistory(withReact(createEditor())), []);
  
  const [value, setValue] = useState<Descendant[]>([
    {
      type: 'paragraph',
      children: [{ text: 'Type your description here...' }],
    },
  ]);

  // Handle changes in the editor
  const handleChange = useCallback((newValue: Descendant[]) => {
    setValue(newValue);
  }, []);

  return (
    <div className="mt-2 w-full px-4 py-2 rounded-xl border border-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500">
      <Slate editor={editor} value={value} onChange={handleChange}>
        <Editable placeholder="Write your product description..." />
      </Slate>
    </div>
  );
};

export default SlateEditor;
